<template>
  <div class="container">
    <router-link to="/" class="back-btn">‚Üê</router-link>
    <div class="catalog-header">
      <h1 class="catalog-title">–ü—Ä–æ—Ñ–∏–ª—å</h1>
    </div>

    <div class="stats-container">
      <!-- –û–±—â–∞—è –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç—å -->
      <div class="stats-block">
        <div class="stats-block-title">–û–±—â–∞—è –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç—å</div>
        <div class="stats-data">
          <div class="stat-item">
            <span class="stat-label">–í—Å–µ–≥–æ –æ–±—ä—è–≤–ª–µ–Ω–∏–π:</span>
            <span class="stat-value">{{ stats.total_ads }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–ö—É–ø–ª–µ–Ω–æ –æ–±—ä—è–≤–ª–µ–Ω–∏–π:</span>
            <span class="stat-value">{{ stats.bought_ads }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–ê–∫—Ç–∏–≤–Ω—ã–µ –∑–∞–∫–∞–∑—ã:</span>
            <span class="stat-value">{{ stats.active_orders }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–û—Ç–º–µ–Ω—ë–Ω–Ω—ã–µ –∑–∞–∫–∞–∑—ã:</span>
            <span class="stat-value">{{ stats.cancelled_orders }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–°—Ä–µ–¥–Ω—è—è —Ü–µ–Ω–∞ —Ä–∞–∑–º–µ—â–µ–Ω–∏—è:</span>
            <span class="stat-value">{{ stats.average_price }}¬†‚ÇΩ</span>
          </div>
        </div>
      </div>

      <!-- –ü–æ–∫—É–ø–∫–∞ —Ä–µ–∫–ª–∞–º—ã –ø–æ —Ç–∏–ø–∞–º -->
      <div class="stats-block">
        <div class="stats-block-title">–ü–æ–∫—É–ø–∫–∞ —Ä–µ–∫–ª–∞–º—ã –ø–æ —Ç–∏–ø–∞–º</div>
        <div class="stats-data">
          <div
            v-for="(count, typeId) in stats.ad_type_purchases"
            :key="typeId"
            class="stat-item"
          >
            <span class="stat-label">–¢–∏–ø {{ typeId }}:</span>
            <span class="stat-value">{{ count }}</span>
          </div>
        </div>
      </div>

      <!-- –ü—Ä–æ–¥–∞–∂–∏ –∏ –≤—ã—Ä—É—á–∫–∞ -->
      <div class="stats-block">
        <div class="stats-block-title">–ü—Ä–æ–¥–∞–∂–∏</div>
        <div class="stats-data">
          <div class="stat-item">
            <span class="stat-label">–í—Å–µ–≥–æ –ø—Ä–æ–¥–∞–∂:</span>
            <span class="stat-value">{{ stats.total_sales }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–í—ã—Ä—É—á–∫–∞ –≤—Å–µ–≥–æ:</span>
            <span class="stat-value">{{ stats.total_revenue }}¬†‚ÇΩ</span>
          </div>
        </div>
      </div>

      <!-- –ü–æ—Å–ª–µ–¥–Ω–∏–µ 30 –¥–Ω–µ–π -->
      <div class="stats-block">
        <div class="stats-block-title">–ó–∞ –ø–æ—Å–ª–µ–¥–Ω–∏–µ 30 –¥–Ω–µ–π</div>
        <div class="stats-data">
          <div class="stat-item">
            <span class="stat-label">–ü—Ä–æ–¥–∞–∂:</span>
            <span class="stat-value">{{ stats.sales_last_30_days }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">–í—ã—Ä—É—á–∫–∞:</span>
            <span class="stat-value">{{ stats.revenue_last_30_days }}¬†‚ÇΩ</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, reactive } from 'vue'
import { useAppStore } from '@/stores/app'

const appStore = useAppStore()

// —Ä–µ–∞–∫—Ç–∏–≤–Ω—ã–π –æ–±—ä–µ–∫—Ç —Å–æ –≤—Å–µ–º–∏ –Ω—É–∂–Ω—ã–º–∏ –ø–æ–ª—è–º–∏
const stats = reactive({
  total_ads: 0,
  bought_ads: 0,
  active_orders: 0,
  cancelled_orders: 0,
  average_price: 0,
  ad_type_purchases: {},
  total_sales: 0,
  total_revenue: 0,
  sales_last_30_days: 0,
  revenue_last_30_days: 0
})

onMounted(async () => {
  const appStore = useAppStore()
  const headers = {
    Authorization: 'Telegram-Init-Data ' + appStore.tgInitDataRow
  }

  try {
    // 1) –ü–æ–ª—É—á–∞–µ–º –æ–±—â—É—é —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É
    const resStats = await fetch(
      `${appStore.apiBaseUrl}/api/my/stats`,
      { headers, mode: 'cors'}
    )
    const dataStats = await resStats.json()
    Object.assign(stats, dataStats)

    // 2) –ü–æ–ª—É—á–∞–µ–º —Å–ø–∏—Å–æ–∫ —Ç–∏–ø–æ–≤ —Ä–µ–∫–ª–∞–º—ã
    const resTypes = await fetch(
      `${appStore.apiBaseUrl}/api/tables/ad_types`,
      { headers, mode: 'cors'}
    )
    const types = await resTypes.json()

    // –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º ad_type_purchases —Å —É—á—ë—Ç–æ–º –ø—Ä–∏—à–µ–¥—à–∏—Ö —Ç–∏–ø–æ–≤
    const purchases = {}
    types.forEach(type => {
      purchases[type.id] = stats.ad_type_purchases[type.id] || 0
    })
    stats.ad_type_purchases = purchases

  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏ –ø—Ä–æ—Ñ–∏–ª—è:', error)
  }
})
</script>
