<template>
  <div class="container">
    <div class="topup-header">
      <router-link to="/" class="back-button">‚Üê</router-link>
      <h1>–ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ –±–∞–ª–∞–Ω—Å–∞</h1>
    </div>

    <div class="form-section">
      <label for="amount">–°—É–º–º–∞ –ø–æ–ø–æ–ª–Ω–µ–Ω–∏—è (‚ÇΩ)</label>
      <input
        v-model.number="amount"
        type="number"
        id="amount"
        placeholder="–í–≤–µ–¥–∏—Ç–µ —Å—É–º–º—É"
        min="10"
      />

      <div class="payment-methods">
        <label>
          <input type="radio" v-model="paymentMethod" value="ton" /> –û–ø–ª–∞—Ç–∞ —á–µ—Ä–µ–∑ TON
        </label>
        <label>
          <input type="radio" v-model="paymentMethod" value="sbp" /> –û–ø–ª–∞—Ç–∞ —á–µ—Ä–µ–∑ –°–ë–ü
        </label>
      </div>

      <button class="top-up-btn" @click="topUp">–ü–æ–ø–æ–ª–Ω–∏—Ç—å</button>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useAppStore } from '@/stores/app'

const appStore = useAppStore()
const amount = ref(null)
const paymentMethod = ref('ton')

async function topUp() {
  if (!amount.value || amount.value < 10) {
    alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —É–∫–∞–∂–∏—Ç–µ —Å—É–º–º—É –Ω–µ –º–µ–Ω–µ–µ 10 ‚ÇΩ')
    return
  }
  if (!paymentMethod.value) {
    alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤—ã–±–µ—Ä–∏—Ç–µ —Å–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã')
    return
  }

  console.log(`topup initdata ${appStore.tgInitDataRaw}`)

  try {
    const headers = {
      'Content-Type': 'application/json',
      Authorization: 'Telegram-Init-Data ' + appStore.tgInitDataRaw
    }
    const body = JSON.stringify({ amount: amount.value })

    const res = await fetch(
      `${appStore.apiBaseUrl}/api/my/balance/deposit`,
      {
        method: 'POST',
        headers,
        body,
        mode: 'cors'
      }
    )

    if (!res.ok) {
      const err = await res.text()
      throw new Error(err || '–û—à–∏–±–∫–∞ –ø–æ–ø–æ–ª–Ω–µ–Ω–∏—è')
    }

    alert('–ë–∞–ª–∞–Ω—Å —É—Å–ø–µ—à–Ω–æ –ø–æ–ø–æ–ª–Ω–µ–Ω –Ω–∞ ' + amount.value + ' ‚ÇΩ')
    // –ü–æ –∂–µ–ª–∞–Ω–∏—é –º–æ–∂–Ω–æ —Ä–µ–¥–∏—Ä–µ–∫—Ç–∏—Ç—å –∏–ª–∏ —Å–±—Ä–∞—Å—ã–≤–∞—Ç—å —Ñ–æ—Ä–º—É
    amount.value = null
  } catch (e) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ø–æ–ª–Ω–µ–Ω–∏–∏ –±–∞–ª–∞–Ω—Å–∞:', e)
    alert('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ø–æ–ª–Ω–∏—Ç—å –±–∞–ª–∞–Ω—Å: ' + e.message)
  }
}
</script>


