<template>
  <div class="container">
    <router-link to="/" class="back-btn">‚Üê</router-link>

    <div class="catalog-header">
      <h1 class="catalog-title">–ö–∞—Ç–∞–ª–æ–≥</h1>
      <div class="search-filters">
        <div class="search-bar">
          <span class="search-icon">üîç</span>
          <input
            type="text"
            class="search-input"
            placeholder="–ü–æ–∏—Å–∫ –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é"
            v-model="search"
          />
        </div>
        <div class="filter-row">
          <select class="filter-select" v-model="selectedSubject">
            <option value="">–í—Å–µ —Ç–µ–º–∞—Ç–∏–∫–∏</option>
            <option
              v-for="subj in subjects"
              :key="subj.id"
              :value="subj.id"
            >
              {{ subj.name }}
            </option>
          </select>
          <select class="filter-select" v-model="sortBy">
            <option value="price_desc">–ü–æ —Ü–µ–Ω–µ (—É–±—ã–≤–∞–Ω–∏—é)</option>
            <option value="price_asc">–ü–æ —Ü–µ–Ω–µ (–≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é)</option>
            <option value="subs_desc">–ü–æ –ø–æ–¥–ø–∏—Å—á–∏–∫–∞–º (—É–±—ã–≤–∞–Ω–∏—é)</option>
            <option value="subs_asc">–ü–æ –ø–æ–¥–ø–∏—Å—á–∏–∫–∞–º (–≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é)</option>
          </select>
        </div>
      </div>
    </div>

    <div class="channels-grid">
      <router-link
        v-for="item in items"
        :key="item.id"
        :to="`/channel/${item.id}`"
      >
        <div class="channel-card">
          <div class="channel-card-icon business">üíº</div>
          <div class="channel-name">{{ item.username }}</div>
          <div class="channel-category">{{ item.subject_ids.join(', ') }}</div>
          <div class="channel-price">–æ—Ç {{ item.price }}¬†‚ÇΩ</div>
        </div>
      </router-link>
    </div>

    <div class="pagination">
      <button
        class="page-btn"
        :class="{ active: page === p }"
        v-for="p in totalPages"
        :key="p"
        @click="page = p"
      >
        {{ p }}
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, watch } from 'vue'
import { useAppStore } from '@/stores/app'

const appStore = useAppStore()
const limit = 10

const subjects = ref([])
const selectedSubject = ref('')
const search = ref('')
const sortBy = ref('price_desc')

const page = ref(1)
const totalPages = ref(1)
const items = ref([])

const headers = () => ({
  Authorization: 'Telegram-Init-Data ' + appStore.tgInitDataRaw
})

async function fetchSubjects() {
  const res = await fetch(
    `${appStore.apiBaseUrl}/api/tables/subjects`,
    { headers: headers(), mode: 'cors', credentials: 'include' }
  )
  subjects.value = await res.json()
}

async function fetchCount() {
  const params = new URLSearchParams({ search: search.value })
  if (selectedSubject.value) params.append('subject_ids', selectedSubject.value)
  const res = await fetch(
    `${appStore.apiBaseUrl}/api/catalog/count?${params.toString()}`,
    { headers: headers(), mode: 'cors', credentials: 'include' }
  )
  const data = await res.json()
  totalPages.value = Math.ceil(data.count / limit) || 1
}

async function fetchItems() {
  const params = new URLSearchParams({
    page: page.value,
    limit,
    search: search.value,
    sort_by: sortBy.value
  })
  if (selectedSubject.value) params.append('subject_ids', selectedSubject.value)
  const res = await fetch(
    `${appStore.apiBaseUrl}/api/catalog?${params.toString()}`,
    { headers: headers(), mode: 'cors', credentials: 'include' }
  )
  items.value = await res.json()
}

onMounted(async () => {
  await appStore.initTelegram()
  await fetchSubjects()
  await fetchCount()
  await fetchItems()
})

watch([search, selectedSubject, sortBy], async () => {
  page.value = 1
  await fetchCount()
  await fetchItems()
})

watch(page, fetchItems)
</script>